.popupBg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(131, 131, 131, 0.1);
    backdrop-filter: blur(10px);
    z-index: 2;
  }
  .popup {
    position: absolute;
    background: #ffffff;
    width: 730px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    padding: 25px 30px 60px 65px;
    text-align: center;
    z-index: 2000;
    align-items: center;
    display: flex;
    flex-direction: column;
  }
  
  .popup__content {
      padding: 0 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  
    .popup__cross {
      align-self: flex-end;
      width: 25px;
      height: 25px;
      color: #2C60E3;
      cursor: pointer;
      margin-bottom: 10px;
    }

    .popup__img {
        width: 49px;
        height: 49px;
        margin-bottom: 10px;
    }
    .popup__title {
        font-size: 18px;
        margin-bottom: 30px;
    }
  
    @media (max-width: 1099px) {
      .popup {
        width: 343px;
        padding: 60px 15px;
      }
  
      .popup__content {
        width: 100%;
        padding: 0;
      }
  }